.App {
  text-align: center;
}

*{
  box-sizing: border-box;
}



.App h1{
  background: rgb(0, 102, 128);
  padding-bottom: 5px;
  color:white;
  border-top: 2px solid black;
  border-bottom: 2px solid black;
}

.mason{
  max-width: 1200px;
  margin:0px;
  padding:0px;
  margin: 0 auto;
}

.imageStyle{
  width:100%;
  box-shadow: 0px 0px 1px .4px rgba(0, 0, 0, 0.664);
  height:auto;
  border-top-right-radius: 4px;
  border-top-left-radius: 4px;
  padding-bottom:0px;
  margin:0px;
}

.relative{
  position: relative;
 max-width: 80vw;
 max-height: auto;
 margin: 0 auto;
}

.cross{
position: absolute;
top:-20px;

color:white;
right:-20px;
background: rgba(48, 3, 3, 0.705);
padding:3px 7px;
border-radius: 10%;
z-index:100;
  font-size: 35px;
}

.cross:hover{
  cursor: pointer;
  background: rgba(255, 255, 255, 0.815);
  color:black;
  transition: all .3s ease-in-out;
  
}

.modal_wrapper{
  background: rgba(0, 0, 0, 0.596);
 

  justify-content: center;
  align-items: center;
  display: none;
  width:100vw;
  height:104vh;
  margin-top: -4vh;
  margin-bottom: -4vh;
  position: fixed;
  z-index:100;
}



.image_wrapper:hover  .arrows{
  opacity:1;
  transition: all .3s ease-in-out;
}

.image_wrapper:hover .imageStyle{
  transition: all .3s ease-in-out;
  filter: grayscale(90%);
  cursor: pointer;
  filter: opacity(0.5);
}

.li{
  width:100%;
  height:auto;

  padding:4px;

}

.info{
  display: flex;
  flex-direction: column;
}

.image_wrapper{
  position:relative;
  padding:0px!important;
  margin:0px!important;
}

.arrows{
  position:absolute;
  top:calc(50% - 25px);
  left:calc(50% - 25px);
  transform: rotate(45deg);
  font-size: 50px;
  z-index:20;
  opacity:0;
  background: rgba(0, 0, 0, 0.705);
  border-radius: 100%;
  padding:10px;
  color:white;
  cursor: pointer;
}

.image_bottom{
  display: flex;
flex-direction: row;
box-shadow: 0px 0px 1px .4px rgba(0, 0, 0, 0.664);
border-bottom-right-radius: 4px;
border-bottom-left-radius: 4px;
flex-wrap: nowrap;
justify-content: space-evenly;
padding:0px!important;
margin:0px!important;


}


.li div {
  background: rgb(235, 226, 226);
 padding:3px;


}

.inViewStyle{
  width:33.33333333333%;
  height:auto;
}

.image_modal{
  width:100%;
  height:100%;
  position:relative;
  box-shadow: 0px 0px 1px 1px black;
  border-radius: 5px;
}

@media only screen and (max-width: 1000px) {
  .cross{
    position: absolute;
    top:-10px;
    right:-10px;

    padding:1px 5px;

      font-size: 25px;
    }
  .inViewStyle{
    width:50%;
    height:auto;
  }
}

.App h1{
  text-align: center;
}

.App input {
  margin-bottom:30px;
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
